import { useCallback, useState } from 'react'
import MD5 from 'crypto-js/md5'

import { ReactComponent as IconPlus } from '../../assets/imgs/plus.svg'
import useLocalStorage from '../../hooks/useLocalStorage'
import styles from './style.module.scss'

export default function TaskInput() {
  const { addTask } = useLocalStorage()
  const [ input ,setInput ] = useState<string>('')

  const addTaskHandle = useCallback(
    (content: string | undefined) => {
      if ( content===undefined || content === "" ) return
      const ctimeValue = new Date().getTime()
      const idValue = MD5(String(ctimeValue)).toString()
      const task = { 
        id: idValue, 
        content: content, 
        important: false, 
        finished: false, 
        ctime: ctimeValue 
      }
      addTask(task)
      setInput('')
  }, [addTask])
  
  return (
    <div className={ styles.taskInput }>
      <IconPlus />
      <input 
        type="text" 
        value={input} 
        onChange={(e) => setInput(e.target.value)}
        placeholder="添加任务"
        onKeyDown={(e) => {e.key === 'Enter' && addTaskHandle(input)}}
      />
    </div>
  )
}
