import { useState } from 'react'
import classNames from 'classnames'
import { useEffect, useMemo } from 'react'
import { BrowserRouter, Routes, Route, NavLink } from 'react-router-dom'

import styles from './style.module.scss'
import { ReactComponent as IconMenu } from '../../assets/imgs/menu.svg'
import { ReactComponent as IconHomeFill } from '../../assets/imgs/home_fill.svg'
import { ReactComponent as IconStarFill } from '../../assets/imgs/star_fill.svg'
import { ReactComponent as IconChecked } from '../../assets/imgs/checked.svg'
import TodoList from '../../components/TodoList'
import Drawer from '../../components/Drawer'
import useLocalStorage from '../../hooks/useLocalStorage'
import { StoreProvider } from '../../hooks/store'
import { ITask } from '../../libs/models'

export default function App() {
  const [ tasks,setTasks ] = useState<ITask[]>([])

  return (
    <StoreProvider value={{ tasks,setTasks }}>
      <Home />
    </StoreProvider>
  )
}

function Home() {
  const { initTasks } = useLocalStorage()
  const [ menuVisible,setMenuVisible ] = useState(false)

  useEffect(() => {
    initTasks()
  }, [initTasks])

  const menus = useMemo(() => {
    return [
      {
        name: '任务',
        link: '/',
        icon: IconHomeFill
      },
      {
        name: '重要',
        link: '/important',
        icon: IconStarFill
      },
      {
        name: '完成',
        link: '/finished',
        icon: IconChecked
      }
    ]
  }, [])
  
  return (
    <BrowserRouter>
      <div className={styles.home}>
        <div className={styles.header}>
          <button className={styles.menuBtn} onClick ={() => setMenuVisible(true)}><IconMenu /></button>
        </div>
        <Drawer placement='left' visible={menuVisible} onClose={() => setMenuVisible(false)} >
          <div className={styles.menu}>
            <div className={styles.menuHeader}>
              <button className={styles.menuBtn} onClick ={() => setMenuVisible(false)}><IconMenu /></button>
            </div>
            <nav className={styles.menuContent}>
              {menus.map((tab, i) => (
                <NavLink
                  to={tab.link}
                  key={i}
                  onClick={() => setMenuVisible(false)}
                  className={({ isActive }) =>classNames(styles.menuItem, {[styles.active]: isActive})}>
                  <tab.icon/>
                  <span>{tab.name}</span>
                </NavLink>
              ))}
            </nav>
          </div>
        </Drawer>
        <main className={styles.main}>
          <Routes>
            <Route path="/" element={ <TodoList type='TASK'/> } />
            <Route path="/important" element={ <TodoList type='IMPORTANT'/>} />
            <Route path="/finished" element={ <TodoList type='FINISHED'/> } />
          </Routes>
        </main>
      </div>
    </BrowserRouter>
  )
}