import classNames from 'classnames'
import { useMemo } from 'react'

import styles from './style.module.scss'

interface Props {
  placement: 'left' | 'bottom' | 'right' | 'top'
  children: React.ReactNode
  visible: boolean
  onClose: () => void
}

export default function Drawer(props: Props) {
 const getclassName = useMemo(() => {
    return classNames(styles.drawer,
      { [styles.visible]:props.visible === true },
      { [styles.left]:props.placement === 'left',
      [styles.bottom]:props.placement === 'bottom',
      [styles.right]:props.placement === 'right',
      [styles.top]:props.placement === 'top'
    })
  } ,[props.placement, props.visible])

  return (
    <div className={getclassName}>
      <div className={styles.drawerMask} onClick={props.onClose} />
      <div className={styles.drawerBody}>
        {props.children}
      </div>
    </div>
  )
}