import classNames from 'classnames'

import { ReactComponent as IconStar } from '../../../assets/imgs/star.svg'
import { ReactComponent as IconStarFill } from  '../../../assets/imgs/star_fill.svg'
import { ReactComponent as IconCheck } from '../../../assets/imgs/check.svg'
import { ReactComponent as IconChecked } from '../../../assets/imgs/checked.svg'
import { ITask } from '../../../libs/models' 
import styles from './style.module.scss'

interface TaskItemProps {
  task: ITask
  setImportant: (id:string) => void
  setFinished: (id:string) => void
  removeTask: (id:string) => void
  onClick?: () => void
}

export default function TaskItem(props: TaskItemProps) {
  return (
    <div className={styles.taskItem}>
      <span className={styles.circleWrap} onClick={() => props.setFinished(props.task.id) }>
        { props.task.finished ? 
          <IconChecked className={styles.checked}/> :
          <IconCheck className={styles.check}/>
        }
      </span>
      <div 
        className=
        {
          props.task.finished?
          classNames(styles.content,styles.finishedText):
          styles.content 
        }
        onClick={props.onClick}
      >
        { props.task.content }
      </div>
      <span className={styles.starWrap} onClick={() => props.setImportant(props.task.id) }>
        { 
          props.task.important ? 
          <IconStarFill className={styles.stared}/> :
          <IconStar className={styles.unstar}/> 
        }
      </span>
    </div>
  )
}
