import { useCallback, useMemo, useState } from 'react'
import classNames from 'classnames'

import { ReactComponent as IconEmpty } from '../../assets/imgs/empty.svg'
import { ReactComponent as IconHomeFill } from '../../assets/imgs/home_fill.svg'
import { ReactComponent as IconStarFill } from  '../../assets/imgs/star_fill.svg'
import { ReactComponent as IconChecked } from '../../assets/imgs/checked.svg'
import TaskItem from '../../components/TodoList/TaskItem'
import useLocalStorage from '../../hooks/useLocalStorage'
import TaskInput from '../TaskInput'
import styles from './style.module.scss'
import Drawer from '../Drawer'


interface TodoListProps {
  type: 'TASK' | 'IMPORTANT' | 'FINISHED'
}

export default function TodoList(props: TodoListProps) {
  const [ opVisible, setOpVisible ] = useState(false)
  const [ currentId, setCurrentId ] = useState('')
  const { 
    tasks, 
    setImportant, 
    setFinished, 
    removeTask 
  } = useLocalStorage()

  const items = useMemo(() => {
    switch (props.type) {
      case 'TASK':
        return tasks.filter(task => !task.finished)
      case 'IMPORTANT':
        return tasks.filter(task => task.important)
      case 'FINISHED':
        return tasks.filter(task => task.finished)
      default:
        return tasks
    }
  }, [props.type, tasks])

  const triggerDrawer = useCallback(
    (id:string) => {
      setCurrentId(id)
      setOpVisible(true)
  },[])
  
  return (
    <>
    <div className={ styles.title }>
      { props.type === 'TASK' && <><IconHomeFill/><span>任务</span></> }
      { props.type === 'IMPORTANT' && <><IconStarFill/><span>重要</span></> }
      { props.type === 'FINISHED' && <><IconChecked/><span>完成</span></> }
    </div>
    <div className={ items.length===0?classNames(styles.todolist, styles.listEmpty):styles.todolist }>
      { items.length === 0 ? <IconEmpty /> :
      items.map(item => (
        <TaskItem
          key={item.id}
          task={item}
          setImportant={() => setImportant(item.id)}
          setFinished={() => setFinished(item.id)}
          removeTask={() => removeTask(item.id)}
          onClick={() => triggerDrawer(item.id)}
        />
      ))}
    </div>
    { props.type === 'TASK' && <TaskInput/> }
    <Drawer
        placement='bottom'
        visible={ opVisible }
        onClose={() => {setOpVisible(false);setCurrentId('')} }
      >
        <div className={styles.op}>
          <div className={styles.opHeader}>选择操作</div>
          <div
            className={styles.opItem}
            onClick ={() => {setFinished(currentId);setOpVisible(false);setCurrentId('')} }
          >{ currentId===''?'':(tasks.filter(task => task.id === currentId)[0].finished?'标记为未完成':'标记为已完成') }</div>
          <div 
            className={styles.opItem} 
            onClick ={() => {setImportant(currentId);setOpVisible(false);setCurrentId('')} }
          >{ currentId===''?'':(tasks.filter(task => task.id === currentId)[0].important?'取消标记重要':'标记为重要') }</div>
          <div
            className={classNames(styles.opItem, styles.opItemRemove)}
            onClick ={() => {removeTask(currentId);setOpVisible(false);setCurrentId('')} }
          >删除任务</div>
          <div className={styles.space}></div>
          <div
            className={styles.opItem}
            onClick ={() => {setOpVisible(false);setCurrentId('')}}
          >取消</div>
        </div>
      </Drawer>
    </>
  )
}